"use client";

import { createContext, useContext, useEffect, useState } from "react";
import axios from "axios";

/* Base URL from env */
const BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL;

const CartContext = createContext<any>(null);

export const CartProvider = ({ children }: { children: React.ReactNode }) => {
  const [cartItems, setCartItems] = useState<any[]>([]);
  const [token, setToken] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);

  /* Load token */
  useEffect(() => {
    setToken(localStorage.getItem("access"));
  }, []);

  const setAuthToken = (newToken: string) => {
    localStorage.setItem("access", newToken);
    setToken(newToken);
  };

  /* Fetch Cart */
  const fetchCart = async (authToken: string | null) => {
    if (!authToken) {
      setCartItems([]);
      setLoading(false);
      return;
    }

    try {
      const res = await axios.get(`${BASE_URL}/user/cart/`, {
        headers: {
          Authorization: `Bearer ${authToken}`,
        },
      });

      setCartItems(res.data);
    } catch (err) {
      console.error("Fetch cart error", err);
      setCartItems([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCart(token);
  }, [token]);

  /* Add to Cart */
  const addToCart = async (
    slug: string,
    size: string,
    authToken: string | null = token
  ) => {
    if (!authToken) return;

    await axios.post(
      `${BASE_URL}/user/cart/add/${slug}/`,
      { size },
      {
        headers: {
          Authorization: `Bearer ${authToken}`,
        },
      }
    );

    fetchCart(authToken);
  };

  /* Remove from Cart */
  const removeFromCart = async (slug: string) => {
    if (!token) return;

    await axios.delete(`${BASE_URL}/user/cart/remove/${slug}/`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });

    fetchCart(token);
  };

  /* Update Quantity */
  const updateQty = async (slug: string, action: "increase" | "decrease") => {
    if (!token) return;

    await axios.patch(
      `${BASE_URL}/user/cart/update/${slug}/`,
      { action },
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );

    fetchCart(token);
  };

  return (
    <CartContext.Provider
      value={{
        cartItems,

        /* total items count */
        cartCount: cartItems.reduce(
          (sum, item) => sum + item.quantity,
          0
        ),

        addToCart,
        removeFromCart,
        updateQty,
        setAuthToken,

        loading,
        token,
      }}
    >
      {children}
    </CartContext.Provider>
  );
};

/* Hook */
export const useCart = () => useContext(CartContext);
















